import React, { useState } from 'react';
// Design Resources
import { Button, Space } from 'antd';
import { CloudUploadOutlined, ExclamationCircleOutlined } from '@ant-design/icons';
// State
import { useGlobalState, useLoading } from '../../../hooks';
// Components
import Title from '../../shared/Title';
import Instruction from '../../shared/Instruction';
import { AdminOnlyButton } from '../../shared/AdminOnly';
import Card from '../../cards/UeSoIssoCard';
import SuggestionCard from './SuggestionCard';

function CompareSuggestionsStep({
  nextGuesser,
  me,
  secretWord,
  suggestions,
  onValidateSuggestions,
  players,
}) {
  const [isLoading] = useLoading();
  const [myRecommendation, setMyRecommendation] = useState(suggestions);
  const [isAdmin] = useGlobalState('isAdmin');

  const onSetValidation = (index, suggestionEntry, notAllowed) => {
    if (notAllowed) return;

    setMyRecommendation((arr) => {
      const newArr = [...arr];
      if (newArr[index]) {
        newArr[index].invalid = !newArr[index].invalid;
      } else {
        newArr[index] = {
          ...suggestionEntry,
          invalid: true,
        };
      }
      return newArr;
    });
  };

  const suggestionsValues = Object.values(myRecommendation);
  const amITheNextGuesser = nextGuesser === me;

  return (
    <div className="u-word-compare-suggestions-step">
      <Title white>Comparem as sugestes</Title>
      <Card word={secretWord.text} header="Palavra Secreta" />
      <Instruction contained>
        J eliminamos todas as palavras iguais, agora, elimine palavras invlidas ou similares.
        <br />
        Lembre-se que so consideradas dicas iguais palavras derividadas,conjugaes: piloto = pilotar =
        pilotando. Variaes como pluralidade, gnero e erros ortogrficos tambm devem ser
        eliminadas:prncipe = princesa = principes = pryncip.
        <br />
        <ExclamationCircleOutlined /> Para no virar baguna, somente{' '}
        <strong>
          <u>{nextGuesser}</u>
        </strong>{' '}
        pode clicar nas palavras para elimin-las ou ativ-las, mas todos podem discutir. <br /> Refiram s
        palavras por nmero, o Adivinhador pode estar ouvindo!
      </Instruction>

      <Space className="u-word-compare-suggestions-step__suggestions">
        {suggestions.map((suggestionEntry, index) => {
          if (!amITheNextGuesser && !isAdmin) {
            return (
              <div key={`${suggestionEntry.suggestion}-${index}`}>
                <SuggestionCard
                  suggestion={suggestionEntry.suggestion}
                  invalid={suggestionEntry.invalid}
                  avatarId={players[suggestionEntry.playerName].avatarId}
                  index={index}
                />
              </div>
            );
          }

          return (
            <button
              key={`${suggestionEntry.suggestion}-${index}`}
              className="u-word-compare-suggestions-step__suggestion-button"
              onClick={() => onSetValidation(index, suggestionEntry, suggestionEntry.invalid)}
            >
              <SuggestionCard
                suggestion={suggestionEntry.suggestion}
                invalid={myRecommendation?.[index]?.invalid ?? suggestionEntry.invalid}
                avatarId={players[suggestionEntry.playerName].avatarId}
                index={index}
              />
            </button>
          );
        })}
      </Space>

      {amITheNextGuesser && (
        <Space className="u-word-compare-suggestions-step__submit">
          <Button
            icon={<CloudUploadOutlined />}
            type="primary"
            onClick={() =>
              onValidateSuggestions({
                validSuggestions: suggestionsValues.filter((suggestion) => !suggestion.invalid),
              })
            }
            disabled={isLoading}
          >
            Confirmar dicas vlidas
          </Button>
        </Space>
      )}

      <AdminOnlyButton
        action={() =>
          onValidateSuggestions({
            validSuggestions: suggestionsValues.filter((suggestion) => !suggestion.invalid),
          })
        }
        label="Confirmar dicas vlidas como Admin"
      />
    </div>
  );
}

export default CompareSuggestionsStep;
